import { FC } from 'react';
import AccentTitle from 'components/AccentTitle';
import BlockFlex from 'components/BlockFlex';
import BenefitItem from './BenefitItem';
import AccentLink from 'components/AccentLink';
import images from 'utils/constants/images'
import './Benefit.scss';

const Benefit: FC = () => {
  const benefits = [
    {
      src: images.bn.star,
      text: '  1         '
    },
    {
      src: images.bn.winne,
      text: '  +     ;)'
    },
    {
      src: images.bn.certification,
      text: '      '
    },
    {
      src: images.bn.gift,
      text: '       '
    },
    {
      src: images.bn.email,
      text: '   email   1    '
    }
  ];

  return (
    <section className='benefit'>
      <div className='benefit__container'>
        <h2 className='benefit__title'>
            {<AccentTitle type='need'></AccentTitle>} 
        </h2>
        <BlockFlex className='benefit__items'>
          {benefits.map(item =>
            <BenefitItem src={item.src} text={item.text} key={item.text} />
          )}
          <BenefitItem src={images.bn.vk} text='    . '>
            <AccentLink src='https://vk.com/' type='main-secondary'>! </AccentLink>
          </BenefitItem>
        </BlockFlex>
      </div>
    </section>
  );
};

export default Benefit;